.img:hover,
.img:focus {
  transform: translateY(-10px) translateZ(0);
}
.latest__posts .posts {
  display: grid;
  grid-column-gap: 5vh;
  grid-template-columns: repeat(3, 1fr);
}
.latest__posts .posts .post {
  display: flex;
  flex-flow: column wrap;
}
.latest__posts .posts .post:nth-child(2) .post__cat {
  order: 2;
}
.latest__posts .posts .post:nth-child(2) .post__date {
  order: 4;
}
.latest__posts .posts .post:nth-child(2) .post__title {
  order: 3;
}
.latest__posts .posts .post:nth-child(2) .post__description {
  order: 4;
}
.latest__posts .posts .post:nth-child(2) .post__img {
  margin-bottom: 2vh;
  order: 1;
}
.latest__posts header h1:before,
.latest__posts header h1:after {
  background: #27a9e3;
}
.urbana__bits {
  display: grid;
  grid-gap: 5vh;
  grid-template-columns: 55vw 1fr;
  position: relative;
}
.urbana__bits .cat a::selection,
.urbana__bits .title a::selection,
.urbana__bits .description a::selection,
.urbana__bits .cat p::selection,
.urbana__bits .title p::selection,
.urbana__bits .description p::selection {
  background: #f4d122;
  color: #fff;
  text-shadow: none;
}
.urbana__bits .cat a {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 #fff inset, 0 -4px 0 0 transparent inset;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.urbana__bits .cat a:hover,
.urbana__bits .cat a:focus {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 #fff inset, 0 -5px 0 0 #f4d122 inset;
}
.urbana__bits .title a {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -10px 0 0 #fff inset, 0 -20px 0 0 transparent inset;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.urbana__bits .title a:hover,
.urbana__bits .title a:focus {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -10px 0 0 #fff inset, 0 -20px 0 0 #f4d122 inset;
}
.urbana__bits .urbana__news {
  display: grid;
  grid-gap: 5vh;
  grid-template-columns: repeat(4, 1fr);
}
.urbana__bits .urbana__news .news {
  display: flex;
  flex-flow: column wrap;
}
.urbana__bits .urbana__news .news .news__img {
  margin-bottom: 2vh;
}
.urbana__bits .urbana__news .news:nth-child(1) {
  flex-flow: row wrap;
  grid-column: 1/-1;
}
.urbana__bits .urbana__news .news:nth-child(1) .news__cat {
  width: 100%;
}
.urbana__bits .urbana__news .news:nth-child(1) .news__title {
  padding-right: 5vh;
  text-align: right;
}
.urbana__bits .urbana__news .news:nth-child(1) .news__title,
.urbana__bits .urbana__news .news:nth-child(1) .news__description {
  flex: 1 0 0;
}
.urbana__bits .urbana__news .news:nth-child(2) {
  grid-column: 1/3;
}
.urbana__bits .urbana__news .news:nth-child(3) {
  grid-column: 3/5;
}
.urbana__bits .urbana__news .news:nth-child(3) .news__cat {
  order: 1;
}
.urbana__bits .urbana__news .news:nth-child(3) .news__title {
  order: 2;
}
.urbana__bits .urbana__news .news:nth-child(3) .news__description {
  order: 3;
}
.urbana__bits .urbana__news .news:nth-child(3) .news__img {
  order: 4;
}
.urbana--brief {
  display: grid;
  grid-gap: 5vh;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 10rem;
}
.urbana--brief header {
  grid-column: 1/-1;
}
.urbana--brief header h1:before,
.urbana--brief header h1:after {
  background: #b011e7;
}
.urbana--brief .news {
  display: flex;
  flex-flow: column wrap;
}
.urbana--brief .news .news__img {
  margin-bottom: 2vh;
}
.urbana--brief .news:nth-child(odd) .news__cat {
  order: 1;
}
.urbana--brief .news:nth-child(odd) .news__title {
  order: 2;
}
.urbana--brief .news:nth-child(odd) .news__img {
  order: 3;
}
.urbana--brief .cat a::selection,
.urbana--brief .title a::selection,
.urbana--brief .description a::selection,
.urbana--brief .cat p::selection,
.urbana--brief .title p::selection,
.urbana--brief .description p::selection {
  background: #b011e7;
  color: #fff;
  text-shadow: none;
}
.urbana--brief .cat a {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 #fff inset, 0 -4px 0 0 transparent inset;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.urbana--brief .cat a:hover,
.urbana--brief .cat a:focus {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 #fff inset, 0 -5px 0 0 #b011e7 inset;
}
.urbana--brief .title a {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -10px 0 0 #fff inset, 0 -20px 0 0 transparent inset;
  color: #000;
  transition: all 0.3s ease-in-out;
}
.urbana--brief .title a:hover,
.urbana--brief .title a:focus {
  box-shadow: 0 -1px 0 0 #fff inset, 0 -10px 0 0 #fff inset, 0 -20px 0 0 #b011e7 inset;
}
.varied {
  display: grid;
  grid-column-gap: 10rem;
  grid-row-gap: 5vh;
  grid-template-columns: 30vw repeat(3, 1fr);
}
.pepsi-chart {
  grid-row: 1/4;
  overflow: hidden;
  padding: 0;
}
.pepsi-chart header h1:before,
.pepsi-chart header h1:after {
  background: #0065c3;
}
.pepsi-chart ul {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: auto;
  width: 100%;
}
.pepsi-chart ul li {
  color: #000;
  display: flex;
  font-family: 'Rubik', sans-serif;
  height: 5rem;
  margin: 0;
  margin-bottom: 1rem;
  opacity: 1;
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease-in-out;
  width: 100%;
}
.pepsi-chart ul li.queda:before,
.pepsi-chart ul li.baja:before,
.pepsi-chart ul li.sube:before {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
}
.pepsi-chart ul li.baja:before,
.pepsi-chart ul li.sube:before {
  border-left: 0.4rem solid transparent;
  border-right: 0.4rem solid transparent;
  height: 0;
  width: 0;
}
.pepsi-chart ul li.queda:before {
  border-bottom: 0.4rem solid transparent;
  border-right: 0.7rem solid rgba(0,0,0,0.5);
  border-top: 0.4rem solid transparent;
  height: 0;
  width: 0;
}
.pepsi-chart ul li.baja:before {
  border-top: 0.7rem solid #f21c1c;
}
.pepsi-chart ul li.sube:before {
  border-bottom: 0.7rem solid #0065c3;
}
.pepsi-chart ul li .cover {
  margin-right: 1.5rem;
  order: 1;
  width: 5rem;
}
.pepsi-chart ul li .cover img {
  height: auto;
  width: 100%;
}
.pepsi-chart ul li .top {
  align-self: center;
  color: #f21c1c;
  font-family: 'candal', sans-serif;
  font-size: 3em;
  margin-right: 2rem;
  order: 2;
  text-align: center;
  width: 10%;
}
.pepsi-chart ul li .album--info {
  align-self: center;
  font-size: 0.9em;
  order: 3;
  text-transform: uppercase;
}
.pepsi-chart ul li .album--info .band {
  font-weight: 700;
}
.pepsi-chart ul li .album--info .song {
  text-transform: capitalize;
}
.efemerides {
  grid-column: 2/5;
  grid-row: 1/2;
  position: relative;
}
.efemerides header h1:before,
.efemerides header h1:after {
  background: #fb3d2b;
}
.efemerides .efem__img {
  height: auto;
  position: relative;
  width: 100%;
}
.efemerides .efem__img:before {
  background: #f4d122;
  content: '';
  height: 6rem;
  position: absolute;
  right: 2rem;
  top: -1rem;
  width: 3rem;
}
.efemerides .efem__img img {
  height: auto;
  width: 100%;
}
.efemerides .efem__title {
  background: #fff;
  font-size: 2.5em;
  left: 0;
  padding: 1rem 1rem 1rem 3rem;
  position: absolute;
  top: 45%;
  z-index: 100;
}
.efemerides .efem__title:before {
  background: #fb3d2b;
  bottom: 1.5rem;
  content: '';
  height: 0.3rem;
  left: 40%;
  position: absolute;
  width: 15rem;
  z-index: -2;
}
.efemerides .efem__description {
  font-weight: 700;
  line-height: 1.5;
  padding: 3rem 0 0 40%;
}
.efemerides .efem__description:before {
  background: #27a9e3;
  border-radius: 50%;
  bottom: -5%;
  content: '';
  height: 10rem;
  position: absolute;
  right: 50%;
  width: 10rem;
  z-index: -10;
}
.tw {
  grid-column: 2/5;
  grid-row: 2/4;
}
.tw header h1:before,
.tw header h1:after {
  background: #0084ff;
}
.tw p {
  line-height: 1.5;
  margin-bottom: 3rem;
  padding: 0 5rem;
}
.tw p a {
  color: #0084ff;
  font-weight: 700;
}
.ig {
  grid-column: 1/-1;
}
.ig header h1:before,
.ig header h1:after {
  background: #fb3958;
}
